.header-leftsidecard {
  width: 100%;
  height: 628px;
  background: #e6f3fa;
  border-radius: 12px;
  background-image: url("../../../images/headerFruit1.png");
  background-repeat: no-repeat;
  background-position: right;
  padding: 125px;
}
.header-leftsidecard p:nth-child(1) {
  font-family: "Garamond";
  font-style: normal;
  font-weight: 400;
  font-size: 27px;
  line-height: 30px;
  letter-spacing: 0.02em;
  text-transform: capitalize;
  margin-bottom: 30px;
  color: #ed8939;
}
.header-leftsidecard h1 {
  font-family: "Nunito";
  font-style: normal;
  font-weight: 700;
  font-size: 54px;
  line-height: 126.72%;
  letter-spacing: 0.02em;
  color: #222222;
  width: 462px;
  max-width: 100%;
}
.header-leftsidecard p:nth-child(3) {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 25px;
  letter-spacing: 0.03em;
  text-transform: capitalize;
  color: #727272;
  margin: 40px 0px;
}
.header-leftsidecard button {
  width: 175px;
  height: 56px;
  font-family: "Open Sans", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #222222;
  border: 2px solid #222222;
  background: transparent;
  outline: none;
  border-radius: 10px;
  cursor: pointer;
}

@media screen and (max-width: 1600px) {
  .header-leftsidecard {
    background-color: #a8defb;
    padding: 50px !important;
  }
  .header-leftsidecard h1 {
    color: #fff;
    width: 100% !important;
  }
  .header-leftsidecard p:nth-child(3) {
    color: #fff !important;
    font-weight: 900;
  }
  .header-leftsidecard button {
    border-color: #fff;
    color: #fff;
    margin: auto;
  }
}/*# sourceMappingURL=HeaderLeftsideCard.css.map */