header {
  height: 942px;
  position: relative;
  .header-leftsidecard,
  .HeaderRightsideCard {
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  }
}
header::before {
  content: "";
  width: 100%;
  height: 100%;
  opacity: 0.03;
  position: absolute;
  left: 0;
  top: 0;
  background-image: url(../../images/headerBg.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

@media screen and (max-width: 1600px) {
  .header-leftsidecard {
    h1 {
      font-size: 35px;
    }
  }
  .header-leftsidecard,
  .HeaderRightsideCard {
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    p,
    h1 {
      color: rgb(255, 255, 255) !important;
      text-shadow: 4px 1px 2px rgba(0, 0, 0, 0.15);
    }
  }
}
@media screen and (max-width: 991px) {
  body {
    .header-leftsidecard {
      display: none;
    }
    header {
      height: auto;
      padding: 150px 0px 100px 0px;
    }
  }
}
