footer {
  padding: 100px 0px 0px 0px;
}

@media screen and (max-width: 1600px) {
  footer .col:nth-child(1) {
    flex: 0 0 100%;
    max-width: 100%;
    margin-bottom: 20px;
  }
  footer .footer-leftside-main {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  footer .footer-leftside-main .footer-leftside {
    width: auto;
    margin-bottom: 100px;
  }
  .col:nth-child(2),
.col:nth-child(3),
.col:nth-child(4) {
    flex: 0 0 50%;
    max-width: 50%;
    margin-bottom: 30px;
  }
}
@media screen and (max-width: 590px) {
  .col:nth-child(2),
.col:nth-child(3),
.col:nth-child(4) {
    flex: 0 0 100%;
    max-width: 100%;
    margin-bottom: 30px;
    border-bottom: 1px solid rgb(231, 231, 231);
  }
}/*# sourceMappingURL=Footer.css.map */