.section4-leftside {
  height: 390px;
  width: 100%;
  background-color: #ffeada;
  border-radius: 10px;
  background-image: url(../../../images/section4-leftside.png);
  display: flex;
  .section4-leftside-text {
    padding: 80px;
    p:nth-child(1) {
      font-family: "Open Sans";
      font-style: normal;
      font-weight: 400;
      font-size: 20px;
      line-height: 22px;
      letter-spacing: 0.025em;
      text-transform: capitalize;
      color: #bc4b68;
    }
    h1 {
      font-family: "Nunito";
      font-style: normal;
      font-weight: 700;
      font-size: 34px;
      line-height: 46px;
      letter-spacing: 0.02em;

      color: #222222;
    }
    p:nth-child(3) {
      font-family: "Open Sans";
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 190%;
      letter-spacing: 0.03em;
      color: #777777;
      width: 332px;
      max-width: 100%;
    }
    button {
      width: 115px;
      height: 42px;
      background: #222222;
      border-radius: 4px;
      font-family: "Open Sans";
      font-style: normal;
      font-weight: 600;
      font-size: 16px;
      line-height: 22px;
      text-align: center;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      color: #ffffff;
      border: 0;
      outline: none;
      cursor: pointer;
    }
  }
  .section4-leftside-image {
    transform: translateX(-100px);
  }
}
@media screen and (max-width: 1600px) {
  .section4-leftside {
    height: auto;
    text-align: center;
    flex-direction: column;
    align-items: center;
    .section4-leftside-image {
      img {
        transform: translateX(-40px);
      }
      margin-top: -50px;
      transform: translateX(0px);
    }
  }
}
