@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@700&family=Open+Sans:wght@400;600&display=swap");
* {
  box-sizing: border-box;
  padding: 0px;
  margin: 0px;
}
.loader {
  display: grid;
  place-items: center;
  span {
    display: none;
  }
}
@media (min-width: 1600px) {
  .container {
    max-width: 1640px;
  }
}
@media (max-width: 1200px) {
  .section1 {
    .col-md-6 {
      -ms-flex: 0 0 50%;
      flex: 0 0 50%;
      max-width: 50%;
      margin-bottom: 20px;
    }
  }
}
@media screen and (max-width: 770px) {
  .section1-card,
  .section2-card,
  .section3-card,
  .section4-card,
  .section5-card,
  .section7-card {
    box-shadow: rgba(0, 0, 0, 0.14) 0px 3px 8px !important;
  }
}
@media (max-width: 600px) {
  .section1 {
    .col-md-6 {
      -ms-flex: 0 0 100%;
      flex: 0 0 100%;
      max-width: 100%;
      margin-bottom: 20px;
    }
  }
}
