.section-header {
  margin-bottom: 50px;
  display: flex;
  justify-content: space-between;
}
.section-header h1 {
  font-family: "Nunito";
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 49px;
  text-transform: capitalize;
  color: #222222;
}
.section-header .section-header-rightside {
  margin-top: 10px;
  display: flex;
}
.section-header .section-header-rightside p {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  text-align: right;
  transform: translateY(6px);
  text-transform: capitalize;
  color: #787878;
  margin-right: 50px;
}
.section-header .section-header-rightside p svg {
  font-size: 10px;
}
.section-header .section-header-rightside .arrowBox1,
.section-header .section-header-rightside .arrowBox2 {
  cursor: pointer;
  width: 38px;
  height: 38px;
  left: 1677px;
  top: 1241px;
  background: #f1f1f1;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  font-size: 30px;
}
.section-header .section-header-rightside .arrowBox2 {
  margin-left: 13px;
  background: #ffc43f;
}

@media screen and (max-width: 590px) {
  .section-header-rightside p {
    display: none;
  }
}/*# sourceMappingURL=SectionHeader.css.map */