.section2-card {
  background: #ffffff;
  border: 1px solid #fbfbfb;
  box-shadow: 0px 5px 22px rgba(0, 0, 0, 0.04);
  border-radius: 16px;
  width: 100%;
  height: 178px;
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 0px 15px;
  .section2-card-text {
    h1 {
      font-family: "Open Sans";
      font-style: normal;
      font-weight: 600;
      font-size: 17px;
      text-transform: capitalize;
      color: #333333;
    }
    p {
      font-family: "Open Sans";
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 19px;
      letter-spacing: 0.02em;
      text-transform: capitalize;
      color: #c7c7c7;
    }
  }
}

@media screen and (max-width: 1600px) {
  .section2-card {
    flex-direction: column;
    padding: 15px;
    height: auto;
    margin-bottom: 20px;
  }
  .section2-card-text {
    text-align: center;
  }
}
