.footer-leftside {
  width: 100%;
}
.footer-leftside img {
  transform: translateX(10px);
}

.footer-social-medias {
  display: flex;
  gap: 14px;
}
.footer-social-medias .social-media-box {
  display: flex;
  justify-content: center;
  transition: 0.5s;
  align-items: center;
  margin-top: 55px;
  width: 45px;
  height: 45px;
  background: #ffffff;
  border: 1px solid #efefef;
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  cursor: pointer;
}
.footer-social-medias .social-media-box svg {
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 20px;
  color: #a5a5a5;
  transition: 0.4s;
}
.footer-social-medias .social-media-box:hover {
  background-color: #edebeb;
}
.footer-social-medias .social-media-box:hover svg {
  color: black;
}/*# sourceMappingURL=FooterLeftside.css.map */