.inputDivMain {
  margin-bottom: 22px;
}
.inputDivMain p {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  transform: translateY(10px);
  line-height: 22px;
  letter-spacing: 0.04em;
  color: #333333;
}
.inputDivMain .inputDiv {
  width: 524px;
  height: 56px;
  background: #ffffff;
  border: 1px solid #ececec;
  border-radius: 10px;
  position: relative;
}
.inputDivMain .inputDiv svg {
  left: 20px;
  top: 50%;
  transform: translateY(-40%);
  position: absolute;
  color: #c8c8c8;
  font-size: 22px;
}
.inputDivMain .inputDiv input {
  color: rgb(169, 169, 169);
  padding: 0px 50px;
  border: 0;
  outline: none;
  width: 100%;
  height: 100%;
  border-radius: 10px;
}

.section6 button {
  width: 100%;
  height: 67px;
  background: #222222;
  border-radius: 10px;
  color: #fff;
  font-family: "Open Sans";
  font-weight: 700;
  border: 0;
  outline: none;
  z-index: 1;
  cursor: pointer;
  transition: 0.3s;
}

.section6 button:hover {
  background-color: rgba(18, 18, 18, 0.809);
}/*# sourceMappingURL=Section6Rightside.css.map */